// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from "./store"

//
import VueAwesomeSwiper from "vue-awesome-swiper"

//
import BottomNavBar from "./globalComponents/BottomNavBar.vue"
import TopSearch from "./globalComponents/TopSearch.vue"
import SingleGuess from "./globalComponents/SingleGuess"

//
import {dataFormat} from "./filters/filters"

Vue.config.productionTip = false
//swiper
Vue.use(VueAwesomeSwiper);

//
Vue.component("BottomNavBar",BottomNavBar)
Vue.component("TopSearch",TopSearch)
Vue.component("SingleGuess",SingleGuess)

//
Vue.filter("dataFormat",dataFormat);

//
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
      document.title = to.meta.title
  }
  next();
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



